<script>
import { defineComponent } from "@vue/composition-api";
import { ethers } from "ethers";
import config from "../utils/config.json";

import holdAbi from "../utils/hold.json";
import routerAbi from "../utils/router.json";
// import farmAbi from "../utils/farm.json";

var provider = new ethers.providers.JsonRpcProvider(
  "https://bsc-dataseed.binance.org/"
);

const hold = new ethers.Contract(
  config.HOLD_CONTRACT_ADDRESS,
  holdAbi,
  provider
);
const router = new ethers.Contract(
  config.PANCAKE_ROUTER_CONTRACT_ADDRESS,
  routerAbi,
  provider
);
// const farm = new ethers.Contract(config.FARM_CONTRACT, farmAbi, provider);
import { onMounted, reactive } from "vue";
import { formatEther, parseEther } from "ethers/lib/utils";
export default defineComponent({
  setup() {
    const state = reactive({
      wbnbBal: 0,
      totalSupply: 0,
      burnRate: 3.00,
      totalMarketCap: 0,
    });

    onMounted(async () => {
      try {
        const amountsOut = await router.getAmountsOut(parseEther("1.0"), [
          config.HOLD_CONTRACT_ADDRESS,
          config.WBNB_ADDRESS,
        ]);

        state.wbnbBal = parseFloat(formatEther(amountsOut[1])).toFixed(3);
      } catch (e) {
        console.log(e);
      }

      try {
        const totalTokenSupply = await hold.totalSupply();
        state.totalSupply = parseFloat(formatEther(totalTokenSupply)).toFixed(
          3
        );
      } catch (e) {
        console.log(e);
      }

      state.totalMarketCap = parseFloat(
        state.wbnbBal * state.totalSupply
      ).toFixed(3);
    });

    return { state };
  },
});
</script>
